import './PaySystems.scss';
// @ts-ignore
import pay from './pay_systems.png';

export const PaySystems = () => {
    return (
        <section className="main-content__pay-systems">
            <div className="section-wrapper">
                <div className="wrapped-platform">
                    <div
                        className="site-title"
                        data-aos="fade-up"
                        data-aos-once="true"
                        data-aos-duration="400"
                        data-aos-delay="150"
                        data-aos-easing="ease-in-out"
                    >
                        <h2>light cost family</h2>
                        <span>    </span>
                    </div>
                    <article className="rules">
                        <div
                            className="rule order-taking"
                            data-aos="fade-up"
                            data-aos-once="true"
                            data-aos-duration="400"
                            data-aos-delay="200"
                            data-aos-easing="ease-in-out"
                        >
                            <strong>   </strong>
                            <time dateTime="10:00-20:00">10:00 - 20:00</time>
                            <strong>  <time dateTime="20:00">20:00</time>      </strong>
                        </div>
                        <div
                            className="rule delivery-time"
                            data-aos="fade-up"
                            data-aos-once="true"
                            data-aos-duration="400"
                            data-aos-delay="250"
                            data-aos-easing="ease-in-out"
                        >
                            <span> </span>
                            <time dateTime="11:00-19:00">11:00 - 19:00</time>
                            <strong>    100-350     .</strong>
                            <strong>     1999 .  *</strong>
                        </div>
                        <div
                            className="rule payment"
                            data-aos="fade-up"
                            data-aos-once="true"
                            data-aos-duration="400"
                            data-aos-delay="300"
                            data-aos-easing="ease-in-out"
                        >
                            <strong>   </strong>
                        </div>
                        <div
                            className="rule footnote"
                            data-aos="fade-up"
                            data-aos-once="true"
                            data-aos-duration="400"
                            data-aos-delay="350"
                            data-aos-easing="ease-in-out"
                        >
                            <strong>*     ,  , ., ., ., ., ., .      </strong>
                        </div>
                    </article>
                    <figure
                        className="pay-systems"
                        data-aos="fade-up"
                        data-aos-once="true"
                        data-aos-duration="400"
                        data-aos-delay="400"
                        data-aos-easing="ease-in-out"
                    >
                        <img src={pay} alt=" " />
                    </figure>
                </div>
            </div>
        </section>
    );
}